.ni {
  position: absolute;
  top: 80px;
  left: 300px;
  background-color: white;
  width: 800px;
  height: 500px;
  border-radius: 10px;
  box-shadow: 10px 10px 50px 0px black;
  overflow: hidden;
  display: none;
}

.ni h1 {
  text-align: center;
  margin-top: -250px;
  color: blue;
}
#nm::placeholder {
  font-weight: bold;
}
#nm {
  width: 300px;
  height: 25px;
  margin-left: 240px;
  border-radius: 10px;
  margin-top: 20px;
  font-size: 20px;
  padding: 15px;
  font-weight: bold;
  border: black 2px dashed;
}
.ni h3 {
  margin-left: 220px;
  color: blue;
}
.drk {
  background-color: black;
  display: inline;
  color: white;
  padding: 7px;
  border-radius: 20px;
  margin-left: 300px;
}
.lgt {
  background-color: rgba(208, 208, 208, 0.527);
  display: inline;
  padding: 7px;
  border-radius: 20px;
  margin-left: 50px;
  font-weight: bold;
}
.skp {
  display: inline;
  background-color: aqua;
  position: absolute;
  padding: 10px;
  font-weight: 600;
  border-radius: 40px;
  top: 450px;
  left: 50px;
}
.accc {
  background-color: aqua;
  color: black;
}

.dn {
  display: inline;
  background-color: aqua;
  position: absolute;
  padding: 10px;
  font-weight: 600;
  border-radius: 40px;
  top: 450px;
  left: 690px;
}
.drk:hover,
.lgt:hover {
  background-color: aqua;
  color: black;
  font-weight: 700;
}
.skp:hover,
.dn:hover {
  transform: scale(1.25);
}
.ni img {
  width: 50%;
}
.mw {
  margin-left: -120px;
  margin-top: 20px;
}
.ww {
  margin-left: 520px;
  margin-top: -160px;
}
.wlc {
  font-size: 40px;
  display: inline-block;
  background: -webkit-linear-gradient(blue, aqua);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  position: absolute;
  top: 20px;
  left: 270px;
  font-weight: 700;
}
@media screen and (max-width: 1135px) {
  .ni {
    left: 100px;
  }
}

@media screen and (max-width: 940px) {
  .ni {
    width: 300px;
    height: 520px;
  }
  .wlc {
    left: 50px;
