  }
  .mw {
    margin-left: -20px;
    margin-top: 70px;
  }
  .ww {
    margin-left: 50px;
    margin-top: 100px;
    position: absolute;
    top: 250px;
  }
  .ni h1 {
    margin-top: 30px;
    font-size: 20px;
  }
  #nm {
    margin-left: 50px;
