@media screen and (max-width: 400px) {
  .ni {
    left: 50px;
    top: 150px;
  }
}

#scr {
  display: none;
}
.list {
  z-index: -20;
  display: inline-flex;
  flex-direction: column;
  row-gap: 100px;
}
.user {
  font-size: 50px;
  color: black;
  margin-top: 120px;
  margin-left: 120px;
  width: 400px;
  display: inline-block;
}
body {
  margin: 0px;
  padding: 0px;
}

.card {
  background-color: black;
  width: 1000px;
  display: inline-block;
  color: white;
  font-size: 2em;
  border-radius: 10px;
  box-shadow: 10px 10px 10px rgb(97, 86, 86);
}
.cards {
  margin-left: 120px;
  display: inline-flex;
  flex-flow: column;
  row-gap: 30px;
  margin-bottom: 100px;
}

.sno {
  display: inline-block;

  margin-left: 30px;
}
.title {
  margin-top: 20px;
  width: 400px;
  margin-left: 20px;
  display: inline-block;
}

.time {
  display: inline-block;

  margin-left: 50px;
  font-size: 0.8em;
}
p {
  margin: 0px;
  padding: 0px;
}
.card img {
  width: 60px;
}
.done {
  margin-bottom: -20px;
  margin-left: 110px;
}
.done:hover {
  transform: scale(1.24);
}
* {
  transition: 0.4s;
}
.del {
  margin-bottom: -20px;
  margin-left: 50px;
}
.del:hover {
  transform: scale(1.54);
}

.cele {
  width: 500px;
  height: 300px;
  margin-top: -100px;
  z-index: -10;
  display: none;
}
.catg {
  display: inline-flex;
  column-gap: 30px;
  color: white;
  margin-left: 120px;
  font-size: 20px;
  width: 900px;
  flex-wrap: wrap;
  row-gap: 20px;
}
.ct {
  background-color: black;
  display: inline-flex;
  border-radius: 10px;
}
.ct:hover {
  background-color: rgba(0, 255, 255);
  color: black;
  transition: 0.1s;
}
.acc {
  background-color: rgba(0, 255, 255);
  color: black;
  transition: 0.1s;
}
.catg h3 {
  margin: 0px;
  cursor: pointer;
  padding: 10px 20px 10px 10px;
  border-radius: 10px;
}
.dell {
  width: 40px;
  padding: 10px;
  display: none;
}
.typewriter h1 {
  font-size: 60px;
}
.aac {
  display: none;
  position: absolute;
  z-index: 4;
  left: 400px;
  top: 200px;
  background-color: white;
  border-radius: 10px;
  box-shadow: 20px 20px 650px 950px rgba(0, 0, 0, 0.752);
}
#aac {
  margin: 100px 0px 100px 120px;
  width: 350px;
  height: 50px;
  border-radius: 30px;
  font-size: 25px;
  padding-left: 20px;
  padding-top: 20px;
}
#acb {
  font-size: 25px;
  position: relative;
  top: -30px;
  left: -250px;
  background-color: aqua;
  padding: 5px;
  width: 100px;
  border-radius: 20px;
}

.ab {
  position: absolute;
  top: 100px;
  left: 580px;
  font-size: 21px;
 
  

  display: inline-block;
}
#abt{
display: none;
}
.ab h1 {
  margin-left: 290px;
}
.ab img {
  margin-left: 200px;
}
.ab p {
  margin-left: -450px;
  width: 500px;
  margin-top: -400px;
  letter-spacing: 1.6px;
}
.ad {
  position: relative;
  top: 700px;
  display: inline-block;
}
.ad h1 {
  margin-left: 200px;
}
.man {
  margin-left: 100px;
  margin-top: 70px;
  animation: blink infinite 2s ease-in-out;
}
.ad p {
  font-size: 20px;
  letter-spacing: 1.6px;
  width: 500px;
  margin-left: 750px;
  margin-top: -500px;
}
.sm img {
  width: 50px;
  margin: 0px;
}
.sm {
  display: inline-block;
  position: relative;
  top: 30px;
  left: 880px;
}
.cm {
  margin-left: 920px;
  font-size: 20px;
  margin-top: 100px;
}
.lnkd {
  padding-right: 20px;
}
.insta {
  padding-right: 20px;
}

.lnkd:hover,
.insta:hover,
.gtb:hover {
  transform: translate(0px, -40px);
  transition: 0.6s ease-in-out;
}
body {
  display: flex;
}
.dt {
  position: absolute;
  left: 1100px;
  color: blue;
}
.t {
  font-size: 30px;
  position: absolute;
  top: -20px;
}
.d {
  position: absolute;
  top: -10px;
  font-weight: 200;
  font-size: 70px;
}
.ln {
  position: absolute;
  top: 100px;
  left: 10px;
  width: 120px;
  height: 1px;
  background-color: black;
  transform: rotate(-60deg);
}
.m {
  position: absolute;
  top: 40px;
  left: 80px;
  font-size: 50px;
  font-weight: 200;
}
.y {
  position: absolute;
  top: 0px;
