  font-size: 30px;
  font-weight: 400;
}
@media screen and (max-width: 1310px) {
  .dt {
    left: 900px;
  }
}
.typewriter {
  margin-top: -100px;
  margin-left: 120px;
}

.typewriter h1 {
  z-index: 2px;

  display: inline-block;
  color: black;
  font-family: monospace;
  overflow: hidden;
  border-right: 0.15em solid black;
  white-space: wrap;
  margin: 0 auto;
  letter-spacing: 0.15em;
  animation: typing 2.5s steps(16, end), blink-caret 0.5s step-end infinite;
}

@keyframes typing {
  from {
    width: 0px;
  }
  to {
    width: 800px;
  }
}

@keyframes blink-caret {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: black;
  }
}

