.sidebar {
  position: fixed;
  height: 100%;
  width: 100px;
  display: inline-block;
  z-index: 1;
}

.sidebar img {
  width: 50%;
  margin-top: 70px;
}
.light {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin-left: 25px;
  position: absolute;
  bottom: 70px;
  background-color: white;
}
.dark {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin-left: 25px;
  position: absolute;
  bottom: 10px;
  background-color: black;
}
.light:hover,
.dark:hover {
  box-shadow: 4px 0px 12px yellow;
}
.home,
.add,
.about {
  padding: 10px;
  padding-left: 25px;
  padding-right: 25px;
}
.home:hover,
.add:hover,
.about:hover {
  background-color: white;
}
.active {
  background-color: white;
}
